import React from "react";
import Icon from "@ezycore/ui/components/atoms/Icon";

export interface InputProps {
  placeholder: string | null;
  name: string;
  icon: string;
}

function Input({ placeholder = null, name, icon }: InputProps) {
  return (
    <div className="input">
      <input
        type="text"
        name={name}
        placeholder={`${placeholder || "Im an input text"}`}
      />
      {icon && (
        <div className="input-icon">
          <Icon icon={icon} />
        </div>
      )}
    </div>
  );
}

export default Input;
